<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card class="box-card">
      <!--  -->
      <el-row>
        <el-col :span="8">
          <el-input
            v-model="queryInfo.query"
            placeholder=""
            class="input-with-select"
            clearable
            @clear="reload"
          >
            <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="tableData" style="width: 100%" border stripe>
        <el-table-column type="index" label="#">
          <template
            v-slot="slotsProps"
          >{{ (queryInfo.pagenum - 1) * queryInfo.pagesize + (slotsProps.$index + 1)}}</template>
        </el-table-column>
        <el-table-column prop="order_number" label="" width="342px"></el-table-column>
        <el-table-column prop="order_price" label="" width="80px"></el-table-column>
        <el-table-column prop="order_pay" label="" width="80px">
          <template v-slot="slotsProps">
            <el-tag type="success" v-if="slotsProps.row.order_pay==='1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="80px"></el-table-column>
        <el-table-column prop="create_time" label="" width="200px">
          <template v-slot="slotsProps">{{slotsProps.row.create_time | dateFormat }}</template>
        </el-table-column>
        <el-table-column label="">
          <template v-slot="slotsProps">
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button type="primary" size="mini" @click="showEditVisible(slotsProps.row)">
                <i class="el-icon-edit"></i>
              </el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top-start"
              :enterable="false"
            >
              <el-button type="success" size="mini" @click="showKuaidiVisible(slotsProps.row)">
                <i class="el-icon-location"></i>
              </el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title="" :visible.sync="editVisible" width="50%" @closed="reset">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="/" prop="addr1">
          <el-cascader v-model="editForm.addr1" :options="citydata" :props="props"></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="addr2">
          <el-input v-model="editForm.addr2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editVisible = false"> </el-button>
        <el-button type="primary"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="kuaidiVisible" width="50%">
      <el-timeline reverse="reverse">
        <el-timeline-item
          v-for="(activity, index) in kuaidiInfo"
          :key="index"
          :timestamp="activity.time"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import citydata from './citydata.js'

export default {
  data() {
    return {
      tableData: [],
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      total: 0,
      editVisible: false,
      editForm: {
        addr1: [],
        addr2: ''
      },
      editFormRules: {
        addr1: [
          {
            required: true,
            message: '/',
            type: 'array',
            trriger: 'blur'
          }
        ],
        addr2: [
          {
            required: true,
            message: '',
            trriger: 'blur'
          }
        ]
      },
      citydata,
      props: {
        expandTrigger: 'hover',
        value: 'value',
        label: 'label',
        children: 'children'
      },
      kuaidiVisible: false,
      kuaidiInfo: []
    }
  },
  created() {
    this.getOrdersList()
  },
  methods: {
    // 
    async getOrdersList() {
      const { data: result } = await this.$http.get('/orders', {
        params: this.queryInfo
      })
      if (result.meta.status !== 200)
        return this.$message.error('')
      this.tableData = result.data.goods
      this.total = result.data.total
    },
    // 
    search() {
      this.queryInfo.pagenum = 1
      this.getOrdersList()
    },
    // 
    reload() {
      this.queryInfo.query = ''
      this.getOrdersList()
    },
    // 
    showEditVisible(row) {
      this.editVisible = true
    },
    // 
    reset() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async showKuaidiVisible(row) {
      const { data: result } = await this.$http.get(`/kuaidi/1106975712662`)

      if (result.meta.status !== 200)
        return this.$message.error('')
      this.kuaidiInfo = result.data
      this.kuaidiVisible = true
      console.log(result.data)
    },
    // 
    handleSizeChange(ps) {
      this.queryInfo.pagenum = 1
      this.queryInfo.pagesize = ps
      this.getOrdersList()
    },
    // 
    handleCurrentChange(pn) {
      this.queryInfo.pagenum = pn
      this.getOrdersList()
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader {
  width: 100%;
}
</style>